.hr {
  border: none;
  border-top: 3px solid $brandRed;
  margin: 30px auto;
  max-width: $containerMaxWidth;
  @include mq(tablet) {
    margin-top: 40px;
    margin-bottom: 40px;
  }
  @include mq(desktop) {
    margin-top: 60px;
    margin-bottom: 60px;
  }

  &.-no-m-top {
    margin-top: 0;
  }

}
