html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  background: $bogyBg;  
  color: $bodyColour;
  font-family: $bodyFont;
  padding-top: $mobileHeaderHeight;
  @include mq(tablet-lg) {
    padding-top: 0;
  }
}

.cf {
  overflow: auto;
}

.cf::after {
  content: "";
  clear: both;
  display: table;
}

#pageContent {
  background: $contentBg;
  padding: 0 0 20px;
  @include mq(desktop) {
    padding-bottom: 40px;
  }
}

h1, h2, h3, h4, h5, h6 {
  font-weight: normal;
  margin: 0;
}

p, li {
  line-height: 1.4;
  margin: 0;
}

.container {
  margin: 0 auto;
  max-width: $containerMaxWidth;
  padding: 0 $containerPaddingMobile;
  @include mq(desktop) {
    padding: 0 $containerPaddingDesktop;
  }
}

.block {
  margin-bottom: 20px;
  @include mq(tablet) {
    margin-bottom: 30px;
  }
}

.fwImg {
  display: block;
  height: auto;
  width: 100%;
}


#sbi_mod_link, #sbi_mod_error {
  float: none !important;
}